import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./screens/Home";
import HostScreen from "./screens/new/Host";
import LaptopScreen from "./screens/new/LaptopScreen";
import NewGuest from "./screens/new/NewGuest";
import SnapshotScreen from "./screens/new/Snapshot";
import Success from "./screens/Success";
import VerifyInfo from "./screens/new/VerifyInfo";
import ReturningGuest from "./screens/returning/ReturningGuest";
import GuestInfo from "./screens/returning/GuestInfo";
import RLaptop from "./screens/returning/RLaptop";
import RHost from "./screens/returning/RHost";
import RSnapshot from "./screens/returning/RSnapshot";
import RVerifyInfo from "./screens/returning/RVerifyInfo";
import Login from "./screens/frontdesk";
import Register from "./screens/frontdesk/Register";
import Dashboard from "./screens/frontdesk/Dashboard";
import NewGuestScreen from "./screens/frontdesk/NewGuestScreen";
import PendingGuest from "./screens/frontdesk/PendingGuest";
import AwaitingHostScreen from "./screens/frontdesk/AwaitingHost";
import Logs from "./screens/frontdesk/Logs";
import TodayLogs from "./screens/frontdesk/Today";
import RejectedScreen from "./screens/frontdesk/Rejected";
import ApprovedScreen from "./screens/frontdesk/Approved";
import Checkedin from "./screens/frontdesk/Checkedin";
import CheckedOut from "./screens/frontdesk/Checkedout";
import Signout from "./screens/Signout";
import PreebookGuest from "./screens/frontdesk/Prebook";
import BookedGuest from "./screens/Booked";
import Staff from "./screens/frontdesk/Staff";
import AdminScreen from "./screens/frontdesk/Admin";
import AwaitingScreen from "./screens/frontdesk/AwatingHostScreen";
import CheckinGuest from "./screens/frontdesk/CheckinGuest";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/newGuest" exact component={NewGuest} />
        <Route path="/laptop" exact component={LaptopScreen} />
        <Route path="/host" exact component={HostScreen} />
        <Route path="/snapshot" exact component={SnapshotScreen} />
        <Route path="/success" exact component={Success} />
        <Route path="/verifyInfo" exact component={VerifyInfo} />
        <Route path="/signout" exact component={Signout} />
        <Route path="/prebookedGuest" exact component={BookedGuest} />
        <Route path="/returning" exact component={ReturningGuest} />
        <Route path="/returning/userInfo" exact component={GuestInfo} />
        <Route path="/returning/laptop" exact component={RLaptop} />
        <Route path="/returning/host" exact component={RHost} />
        <Route path="/returning/snapshot" exact component={RSnapshot} />
        <Route path="/returning/verifyInfo" exact component={RVerifyInfo} />
        <Route path="/frontdesk" exact component={Login} />
        <Route path="/frontdesk/register" exact component={Register} />
        <Route path="/frontdesk/staff" exact component={Staff} />
        <Route path="/frontdesk/admin" exact component={AdminScreen} />
        <Route path="/frontdesk/dashboard" exact component={Dashboard} />
        <Route path="/frontdesk/prebook" exact component={PreebookGuest} />
        <Route path="/frontdesk/newGuest" exact component={NewGuestScreen} />
        <Route path="/frontdesk/approved" exact component={ApprovedScreen} />
        <Route path="/frontdesk/rejected" exact component={RejectedScreen} />
        <Route path="/frontdesk/checkedin" exact component={Checkedin} />
        <Route path="/frontdesk/checkedout" exact component={CheckedOut} />
        <Route
          path="/frontdesk/awaitingHost"
          exact
          component={AwaitingHostScreen}
        />
        <Route path="/frontdesk/guest/:id" exact component={PendingGuest} />
        <Route
          path="/frontdesk/guest/awaiting/:id"
          exact
          component={AwaitingScreen}
        />
        <Route
          path="/frontdesk/guest/checkin/:id"
          exact
          component={CheckinGuest}
        />
        <Route path="/frontdesk/logs" exact component={Logs} />
        <Route path="/frontdesk/logs/today" exact component={TodayLogs} />
      </Switch>
    </Router>
  );
}

export default App;
