import React, { useState, useEffect } from "react";
import Card from "../../components/UI/Card";
import styles from "../styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import { useSelector, useDispatch } from "react-redux";
import { getNewEmployee } from "../../redux/actions/adminActions";

const HostScreen = ({ history }) => {
  const h = localStorage.getItem("host");
  const p = localStorage.getItem("purpose");
  const a = localStorage.getItem("appointment");
  const [host, setHost] = useState(h ? h : "");
  const [purpose, setPurpose] = useState(p ? p : "");
  const [appointment, setAppointment] = useState(a ? a : "");

  const submitHandler = (e) => {
    e.preventDefault();
    localStorage.setItem("host", host);
    localStorage.setItem("purpose", purpose);
    localStorage.setItem("appointment", appointment);
    history.push("/snapshot");
  };

  const dispatch = useDispatch();
  const getStaff = useSelector((state) => state.getStaff);
  const { staff } = getStaff;

  useEffect(() => {
    dispatch(getNewEmployee());
  }, [dispatch]);

  return (
    <div className={styles.app}>
      <Card title="Welcome" content="Whom are you here to see" />
      <div className={styles.form}>
        <form onSubmit={submitHandler}>
          <div className={styles.formContainer}>
            <select
              type="text"
              className="form-control"
              onChange={(e) => setHost(e.target.value)}
            >
              <option>{h ? h : "Whom are you here to see?"}</option>
              {staff &&
                staff.map((item, i) => (
                  <option value={item.email} key={i}>
                    {item.fullname}
                  </option>
                ))}
            </select>
          </div>
          <div className={styles.formContainer}>
            <select
              type="text"
              className="form-control"
              onChange={(e) => setPurpose(e.target.value)}
            >
              <option>{p ? p : "Select Purpose of Visit"}</option>
              <option value="Personal">Personal</option>
              <option value="Official">Official</option>
            </select>
          </div>
          <div className={styles.formContainer}>
            <select
              type="text"
              className="form-control"
              onChange={(e) => setAppointment(e.target.value)}
            >
              <option>{a ? a : "Do you have an appointment"}</option>
              <option value="Yes">Yes</option>
              <option value="No">No</option>
            </select>
          </div>
          <div className={styles.formContainer}>
            <div className="col-md-3 float-start">
              <Link
                to="/laptop"
                className={`btn btn-dark form-control  ${styles.expandBtn}`}
              >
                <FiArrowLeftCircle /> Back
              </Link>
            </div>
            <div className="col-md-3 float-end">
              <button
                type="submit"
                className={`btn btn-success form-control ${styles.expandBtn}`}
                value="Next"
              >
                <FiArrowRightCircle /> Next
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default HostScreen;
