import React, { useState } from "react";
import Card from "../components/UI/Card";
import styles from "./styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import { useDispatch, useSelector } from "react-redux";
import { checkoutGuest } from "../redux/actions/guestActions";
import Loader from "./Loader";
import { CHECKOUT_RESET } from "../redux/constants/guestConstants";

const Signout = ({ history }) => {
  const [mobile, setMobile] = useState("");
  const timeOut = new Date().toLocaleString();
  var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");

  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();

    dispatch(checkoutGuest(mobile, timeOut, date));
  };

  const checkout = useSelector((state) => state.checkout);
  const { loading, error, success } = checkout;

  if (success) {
    dispatch({ type: CHECKOUT_RESET });
    history.push("/");
  }

  return (
    <div className={styles.app}>
      <Card title="Welcome" content="Welcome Back!" />
      {loading ? (
        <Loader />
      ) : (
        <div className={styles.form}>
          {error && <div className="alert alert-danger">{error}</div>}
          <form onSubmit={submitHandler}>
            <div className={styles.formContainer}>
              <input
                type="tel"
                className="form-control"
                onChange={(e) => setMobile(e.target.value)}
                placeholder="Mobile"
                value={mobile}
              />
            </div>
            <div className={styles.formContainer}>
              <div className="col-md-3 float-start">
                <Link
                  to="/"
                  className={`btn btn-dark form-control  ${styles.expandBtn}`}
                >
                  <FiArrowLeftCircle /> Back
                </Link>
              </div>
              <div className="col-md-3 float-end">
                <button
                  type="submit"
                  className={`btn btn-success form-control ${styles.expandBtn}`}
                  value="Next"
                >
                  <FiArrowRightCircle /> Signout
                </button>
              </div>
            </div>
          </form>
        </div>
      )}
    </div>
  );
};

export default Signout;
