import React, { useState } from "react";
import Card from "../components/UI/Card";
import styles from "./styles.module.css";
import { Link } from "react-router-dom";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import { useDispatch, useSelector } from "react-redux";
import { validatePrebook } from "../redux/actions/guestActions";
import Loader from "./Loader";

const BookedGuest = ({ history }) => {
  const [token, setMobile] = useState("");
  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();

    dispatch(validatePrebook(token));
  };

  const sbook = useSelector((state) => state.sbook);
  const { loading, error, success } = sbook;

  if (success) {
    history.push("/");
  }

  return (
    <div className={styles.app}>
      <Card title="Welcome" content="Welcome Back!" />
      {loading ? (
        <Loader />
      ) : (
        <div className={styles.form}>
          {error && <div className="alert alert-danger">{error}</div>}
          <form onSubmit={submitHandler}>
            <div className={styles.formContainer}>
              <input
                type="text"
                className="form-control"
                onChange={(e) => setMobile(e.target.value)}
                placeholder="Token"
                value={token}
              />
            </div>
            <div className={styles.formContainer}>
              <div className="col-md-3 float-start">
                <Link
                  to="/"
                  className={`btn btn-dark form-control  ${styles.expandBtn}`}
                >
                  <FiArrowLeftCircle /> Back
                </Link>
              </div>
              <div className="col-md-3 float-end">
                <button
                  type="submit"
                  className={`btn btn-success form-control ${styles.expandBtn}`}
                  value="Next"
                >
                  <FiArrowRightCircle /> Proceed
                </button>
              </div>
            </div>
          </form>
        </div>
      )}
    </div>
  );
};

export default BookedGuest;
